import { SVG, Line } from '@svgdotjs/svg.js';

export function Draw() {
  const draw = SVG().addTo('#canvas').size(800, 600);
  let isDrawing = false;
  let line: Line | null = null;

  const onMouseDown = (event: MouseEvent & { offsetX: number; offsetY: number }) => {
    isDrawing = true;
    const { offsetX, offsetY } = event;
    line = draw.line(offsetX, offsetY, offsetX, offsetY).stroke({ width: 2, color: 'black' });
  };

  const onMouseMove = (event: MouseEvent & { offsetX: number; offsetY: number }) => {
    if (isDrawing && line) {
      const { offsetX, offsetY } = event;
      line.plot(offsetX, offsetY, offsetX, offsetY);
    }
  };

  const onMouseUp = () => {
    isDrawing = false;
    line = null;
  };

  draw.node.addEventListener('mousedown', onMouseDown);
  draw.node.addEventListener('mousemove', onMouseMove);
  draw.node.addEventListener('mouseup', onMouseUp);
}

document.addEventListener('DOMContentLoaded', Draw);
